                                </td>
                                <td>
                                    <input class="form-control text-right" v-model="row.price" number data-type="currency" type="number"/>
                                </td>
                                <td>
                                    <input class="form-control text-right" v-model="row.discount" number data-type="currency" type="number"/>
                                </td>
                                <td>
                                    <select class="form-control" v-model="row.tax" v-on:change="taxCalc">
                                        <option value="0">0%</option>
                                        <option value="5">5%</option>
                                        <option value="12">12%</option>
                                        <option value="18">18%</option>
                                        <option value="28">28%</option>
                                    </select>
                                    <ul>
                                      <li>CGST: {{row.cgst}}</li><br/>
                                      <li>SGST: {{row.sgst}}</li><br/>
                                      <li>IGST: {{row.igst}}</li>
                                    </ul>
