                                    <input class="form-control text-right" :value="row.qty * row.price" v-model="row.total" number readonly />
                                </td>
                                <td>
                                    <button class="btn btn-danger btn-xs" @click="removeRow(index)"><icon name="trash" scale="1"></icon></button>
                                </td>
