                            <tr>
                                <td colspan="7" class="text-right"><strong>GRANDTOTAL</strong></td>
                                <td colspan="1" class="text-right"><strong>{{ grandtotal }}</strong></td>
                                <td></td>
                            </tr>
                            </tfoot>
                        </table>
                        <!-- <pre>{{ $data | json }}</pre> -->
                    </div>

 <br>
 <b-btn @click="saveOrUpdate()" variant='primary'>Save</b-btn>
 <b-btn @click="clearInvTran()" variant='primary'>Cancel</b-btn>
<hr />
 <div class="justify-content-centermy-1 row">

   <b-form-fieldset horizontal label="Rows per page" class="col-6" :label-size="6">
     <b-form-select :options="[{text:10,value:10},{text:20,value:20},{text:50,value:50}]" v-model="perPage">
     </b-form-select>
   </b-form-fieldset>

   <b-form-fieldset horizontal label="Filter" class="col-6" :label-size="2">
     <b-form-input v-model="filter" placeholder="Type to Search"></b-form-input>
   </b-form-fieldset>
 </div>

 <!-- Main table element -->
  <b-table striped hover :items="invMstr" :fields="fields" :current-page="currentPage" :per-page="perPage" :filter="filter">
    <template slot="index" scope="data">
        {{data.index + 1}}
      </template> <template slot="invoiceId" scope="item">
     {{item.value}}
   </template> <template slot="invoiceDate" scope="item">
     {{item.value | moment("DD-MM-YYYY")}}
   </template><template slot="name1" scope="item">
     {{item.value}}
   </template> <template slot="grandtotal" scope="item">
     {{item.value}}
   </template> <template slot="update" scope="item">
      <b-btn size="sm" @click="update(item.item)">Update</b-btn>
    </template></b-table>
  <div class="justify-content-center row my-1">
    <b-pagination size="md" :total-rows="this.invMstr.length" :per-page="perPage" v-model="currentPage" /> </div>
  <!-- Modal Component -->
  <b-modal id="modal1" size="sm" title="Enter Company Details" ok-only=true @ok="submit" @shown="loadModalData">
    <form>
      <b-alert variant="danger" dismissible :show="showDismissibleAlert" required></b-alert>
      <div class='group'> <input class='ipc' v-model='cname' type='text' required> <span class='highlight'></span> <span class='bar'></span> <label>{{ 'Name' }}</label> </div>
      <div class='group'> <input class='ipc' v-model='cgstin' type='text' required> <span class='highlight'></span> <span class='bar'></span> <label>{{ 'GSTIN' }}</label> </div>
      <div class='group'> <input class='ipc' v-model='invf' type='text' required> <span class='highlight'></span> <span class='bar'></span> <label>{{ 'Invoice Fixed' }}</label> </div>
      <div class='group'> <input class='ipc' v-model='invr' type='text' required> <span class='highlight'></span> <span class='bar'></span> <label>{{ 'Invoice Serial' }}</label> </div>
      <b-form-select v-model='cstate' :options='stateList' calss='mb-3' style="margin-top:-35px"></b-form-select>
      <b-form-input textarea v-model="caddress" placeholder="Address"></b-form-input>
    </form>
  </b-modal>
</div>
</template>
</div>
</template>
<script>
import Vue from 'vue'
import AutocompleteVue from 'autocomplete-vue'
import Datepicker from 'vuejs-datepicker'
import swal from 'sweetalert2'
// import localforage from 'vue-localforage'
Vue.component('Datepicker', Datepicker)
Vue.component('autocomplete-vue', AutocompleteVue)
Vue.component('rows-component', {
  components: {
    alert
  },
  props: ['row', 'index'],
  template: `
        <div class="row">
        <span>
        {{ index+1 }}
        </span>
        <div><b-form-input v-model="row.description" type="text" placeholder="Enter your name""></b-form-input></div>
        <div><input type="text" v-model="row.hsn"></div>
        <input type="text" v-model="row.qty">
        <input type="text" v-model="row.unitPrice">
        <input type="text" v-model="row.total">
        <input type="text" v-model="row.discount">
        <input type="text" v-model="row.taxableAmt">
        <input type="text" v-model="row.cgst">
        <input type="text" v-model="row.sgst">
        <input type="text" v-model="row.igst">
        <button type="button" class="button btn-primary" @click="removeRow(index)">Remove</button>
        </div>`,
  watch: {
    row: {
      handler: function (newValue) {
        console.log('description:' + newValue.description)
        console.log('New hsn: ' + newValue.hsn)
        newValue.total = newValue.price * newValue.qty
        console.log(newValue.total)
      },
      deep: true
    }
  },
  methods: {
    removeRow: function (index) {
      console.log()
      // this.rows.splice(row, 1)
      this.$parent.rows.splice(index, 1)
    }
  }
})
import stateData from '../assets/state.json'
var stateList = stateData
export default {
  name: 'test',
  data: function () {
    return {
      gstin1: '',
      gstin2: '',
      gstinl: 'GSTIN  ',
      name1: '',
      name2: '',
      namel: 'Name  ',
      address1: '',
      address2: '',
      addressl: 'Address  ',
      statel: 'State  ',
      stateCode1: null,
      stateCode2: null,
      cstate: '',
      stateCodel: 'State Code  ',
      invoiceId: '',
      invoiceIdl: 'Invoice No  ',
      invoiceDate: new Date(),
      invoiceDatel: 'Invoice Date  ',
      sameDet: false,
      stateList: stateList,
      goodsnservice: 'G',
      taxtotal: 0,
      total: 0,
      rows: [{
        description: '',
        hsn: '',
        qty: 0,
        price: 0,
        total: 0,
        discount: 0,
        taxableAmt: 0,
        cgst: 0,
        sgst: 0,
        igst: 0,
        tax: ''
      }],
      grandtotal: 0,
      showDismissibleAlert: false,
      cgstin: '',
      cname: '',
      caddress: '',
      invf: '',
      invr: '',
      invMstr: [],
      perPage: 10,
      filter: '',
      currentPage: 1,
      fields: {
        index: {
          label: '#',
          sortable: false
        },
        invoiceId: {
          label: 'Invoice Id',
          sortable: true
        },
        invoiceDate: {
          label: 'Invoice Date',
          sortable: true
        },
        name1: {
          label: 'Customer Name',
          sortable: true
        },
        grandtotal: {
          label: 'Total',
          sortable: true
        },
        update: {
          label: 'Update',
          sortable: false
        }
      },
      isUpdate: false
    }
  },
  mounted: function () {
    var self = this
    // self.loadModalData()
    this.$getItem('companyData', function (err, value) {
      setTimeout(function () {
        if (err) {
          console.log(err.stack)
        }
        if (value !== null && value !== undefined) {
          self.cname = value.cname
          self.cgstin = value.cgstin
          self.caddress = value.caddress
          self.invf = value.invf
          self.invr = value.invr
          self.cstate = value.cstate
        }
      })
    }, 2000)
    this.loadInvMstr()
  },
  computed: {
    sameState: function () {
      var sstate = false
      if (this.stateCode1 === this.cstate) sstate = true
      return sstate
    },
    invoiceIdComp: function () {
      if (this.isUpdate) {
        return this.invoiceId
      } else {
        let f = this.invf
        let r = this.invr
        return f + r
      }
    }
  },
  methods: {
    update: function (value) {
      this.isUpdate = true
      this.gstin1 = value.gstin1
      this.gstin2 = value.gstin2
      this.name1 = value.name1
      this.name2 = value.name2
      this.address1 = value.address1
      this.address2 = value.address2
      this.stateCode1 = value.stateCode1
      this.stateCode2 = value.stateCode2
      this.rows = value.rows
      this.invoiceId = value.invoiceId
      this.invoiceDate = value.invoiceDate
      window.scrollTo(0, 0)
    },
    loadModalData: function () {
      let self = this
      this.$getItem('companyData', function (err, value) {
        if (err) {
          console.log(err.stack)
        }
        if (value !== null && value !== undefined) {
          self.cname = value.cname
          self.cgstin = value.cgstin
          self.caddress = value.caddress
          self.invf = value.invf
          self.invr = value.invr
          self.cstate = value.cstate
        }
      })
    },
    incrInvId: function () {
      if (!this.isUpdate) {
        let self = this
        this.$getItem('companyData', function (err, value) {
          if (err) {
            console.log(err.stack)
          }
          if (value !== null && value !== undefined) {
            value.invr = parseFloat(value.invr) + 1
            self.$setItem('companyData', value)
            self.invf = value.invf
            self.invr = value.invr
          }
        })
      }
    },
    loadInvMstr: function () {
      var self = this
      this.$getItem('invoiceMstr', function (err, value) {
        if (err) {
          console.log()
        } else {
          if (value !== null && value !== undefined) {
            self.invMstr = value
          } else {
            self.invMstr = []
          }
        }
      })
    },
    saveOrUpdate: function () {
      if (this.isUpdate) {
        this.updateInv()
      } else {
        this.saveInv()
      }
    },
    updateInv: function () {
      let self = this
      this.invMstr.forEach(function (inv) {
        if (inv.invoiceId === self.invoiceId) {
          inv.gstin1 = self.gstin1
          inv.gstin2 = self.gstin2
          inv.name1 = self.name1
          inv.name2 = self.name2
          inv.address1 = self.address1
          inv.address2 = self.address2
          inv.stateCode1 = self.stateCode1
          inv.stateCode2 = self.stateCode2
          inv.cstate = self.cstate
          inv.invoiceIdComp = self.invoiceIdComp
          inv.invoiceDate = self.invoiceDate
          inv.sameDet = self.sameDet
          inv.taxtotal = self.taxtotal
          inv.total = self.total
          inv.rows = self.rows
          inv.sameState = self.sameState
          inv.invoiceIdComp = self.invoiceIdComp
          inv.grandtotal = self.grandtotal
        }
      })
      self.$setItem('invoiceMstr', this.invMstr)
      self.$nextTick(function () {
        swal({
          title: '<h3>Invoice Id: <b>' + this.invoiceIdComp + ' Updated Successfully.</b></h3>',
          type: 'success'
        })
        self.clearInvTran()
        self.loadInvMstr()
      })
    },
    saveInv: function () {
      let gstin1 = this.gstin1
      let gstin2 = this.gstin2
      let name1 = this.name1
      let name2 = this.name2
      let address1 = this.address1
      let address2 = this.address2
      let stateCode1 = this.stateCode1
      let stateCode2 = this.stateCode2
      let cstate = this.cstate
      let invoiceId = this.invoiceIdComp
      let invoiceDate = this.invoiceDate
      let sameDet = this.sameDet
      let taxtotal = this.taxtotal
      let total = this.total
      let rows = this.rows
      let sameState = this.sameState
      let invoiceIdComp = this.invoiceIdComp
      let grandtotal = this.grandtotal
      let goodsnservice = this.goodsnservice
      let self = this
      this.$getItem('invoiceMstr', function (err, value) {
        if (err) {
          console.log(err.stack)
        }
        var invJson = {
          gstin1,
          gstin2,
          name1,
          name2,
          address1,
          address2,
          stateCode1,
          stateCode2,
          cstate,
          invoiceId,
          invoiceDate,
          sameDet,
          taxtotal,
          total,
          rows,
          sameState,
          invoiceIdComp,
          grandtotal,
          goodsnservice
        }
        if (value !== null && value !== undefined) {
          value.push(invJson)
          self.$setItem('invoiceMstr', value)
        } else {
          let tempArr = []
          tempArr.push(invJson)
          self.$setItem('invoiceMstr', tempArr)
        }
        self.$nextTick(function () {
          swal({
            title: '<h3>Invoice Created Successfully With Invoice Id: <b>' + this.invoiceIdComp + '</b></h3>',
            type: 'success'
          })
          self.clearInvTran()
          self.incrInvId()
          self.loadInvMstr()
        })
      })
    },
    clearInvTran: function () {
      this.gstin1 = ''
      this.gstin2 = ''
      this.name1 = ''
      this.name2 = ''
      this.address1 = ''
      this.address2 = ''
      this.stateCode1 = null
      this.stateCode2 = null
      this.cstate = ''
      this.taxtotal = 0
      this.total = 0
      this.invoiceDate = new Date()
      this.rows = [{
        description: '',
        hsn: '',
        qty: 0,
        price: 0,
        total: 0,
        discount: 0,
        taxableAmt: 0,
        cgst: 0,
        sgst: 0,
        igst: 0,
        tax: ''
      }]
      this.isUpdate = false
    },
    saveInvTran: function (tempArr) {
      this.$setItem('invoiceMstr', tempArr)
    },
    submit: function (e) {
      let cname = this.cname
      let cgstin = this.cgstin
      let caddress = this.caddress
      let invf = this.invf
      let invr = this.invr
      let cstate = this.cstate
      this.$setItem('companyData', {
        cname,
        cgstin,
        caddress,
        cstate,
        invf,
        invr
      })
    },
    addRow: function (index) {
      try {
        this.rows.push({
          description: '',
          hsn: '',
          qty: 0,
          price: 0,
          total: 0,
          discount: 0,
          taxableAmt: 0,
          cgst: 0,
          sgst: 0,
          igst: 0
        })
      } catch (e) {
        console.log(e)
      }
    },
    removeRow: function (index) {
      this.rows.splice(index, 1)
    },
    taxCalc: function () {
      var t = 0
      var tt = 0
      var sstate = this.sameState
      this.rows.forEach(function (modRw) {
        let lineTotal = modRw.qty * modRw.price
        let lineDisc = modRw.discount > 0 ? (lineTotal * modRw.discount) / 100 : 0
        let netLineTotal = lineTotal - lineDisc
        let lineTax = modRw.tax === undefined || modRw.tax === '' ? 0 : parseFloat(modRw.tax)
        modRw.taxableAmt = netLineTotal * lineTax / 100
        modRw.total = netLineTotal
        if (sstate) {
          let amt = modRw.taxableAmt / 2
          modRw.cgst = amt
          modRw.sgst = amt
          modRw.igst = 0
        } else {
          modRw.cgst = 0
          modRw.sgst = 0
          modRw.igst = modRw.taxableAmt
        }
        t += modRw.total
        tt += modRw.taxableAmt
      })
      this.taxtotal = isNaN(tt) ? 0 : tt
      this.total = t
      this.grandtotal = this.taxtotal + this.total
    },
    print: function () {
      var mywindow = window.open('', 'PRINT', 'height=400,width=600')
      mywindow.document.write('<html><head><title>' + document.title + '</title>')
      mywindow.document.write('</head><body >')
      mywindow.document.write('<h1>' + document.title + '</h1>')
      mywindow.document.write(document.getElementById('appm').innerHTML)
      mywindow.document.write('</body></html>')
      mywindow.document.close() // necessary for IE >= 10
      mywindow.focus() // necessary for IE >= 10*/
      mywindow.print()
      mywindow.close()
      return true
    }
  },
  watch: {
    cstate: function () {
      this.taxCalc()
      this.loadInvMstr()
    },
    sameDet: function () {
      if (this.sameDet) {
        this.name2 = this.name1
        this.gstin2 = this.gstin1
        this.stateCode2 = this.stateCode1
        this.address2 = this.address1
      }
    },
    name1: function () {
      if (this.sameDet) {
        this.name2 = this.name1
      }
    },
    gstin1: function () {
      if (this.sameDet) {
        this.gstin2 = this.gstin1
      }
    },
    stateCode1: function () {
      if (this.sameDet) {
        this.stateCode2 = this.stateCode1
      }
      this.taxCalc()
    },
    address1: function () {
      if (this.sameDet) {
        this.address2 = this.address1
      }
      this.taxCalc()
    },
    rows: {
      handler: function (event) {
        this.taxCalc()
      },
      deep: true
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped>
